*, *::before, *::after {
  box-sizing: border-box;
}

body {
  font-family: $font-noto, $font-roboto, $font-family-all;
  font-size: 16px;
  line-height: 1.5;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
a {
  display: block;
  text-decoration: none;
}

